import React, { useState, useEffect, Suspense } from "react";
import { Routes, Route, useLocation } from "react-router-dom";
// import Loading from "../Loading/Loading";
import axios from "axios";
import { AnimatePresence } from "framer-motion";
import Countries from "../Countries/Countries";
import CountryDetails from "../CountryDetails/CountryDetails";
// const Countries = React.lazy(() => import("../Countries/Countries"));
// const CountryDetails = React.lazy(() =>
//   import("../CountryDetails/CountryDetails")
// );

function Routess({ setLoading }) {
  const location = useLocation();
  const [countries, setCountries] = useState([]);
  const [searchCountries, setSearchCountries] = useState([...countries]);
  // const [searchInput, setSearchInput] = useState("");
  const [page, setPage] = useState(0);
  const [region, setRegion] = useState("");
  //preloader

  const search = (countryName) => {
    setPage(0);
    let filterdCountries = countries.filter((country) =>
      country.name.common.toLowerCase().includes(countryName.toLowerCase())
    );
    setSearchCountries(filterdCountries);
  };
  useEffect(() => {
    axios
      .get("https://restcountries.com/v3.1/all")
      .then((res) => {
        let legalCountries = res.data.filter(
          (country) => country.name.common !== "Israel"
        );
        setCountries(legalCountries);
        setLoading(false);
      })
      .catch((err) => console.log(err));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    setSearchCountries(countries);
  }, [countries]);

  useEffect(() => {
    if (region === "") {
      setSearchCountries(countries);
    } else {
      let arr = countries.filter((country) => country.region === region);
      setSearchCountries(arr);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [region]);

  return (
    <AnimatePresence>
      <Routes location={location} key={location.pathname}>
        <Route
          path="/countryDetails/:countryName"
          exact
          element={
            // <Suspense fallback={<Loading />}>
            <CountryDetails
              setSearchCountries={setSearchCountries}
              countries={countries}
            />
            // </Suspense>
          }
        ></Route>
        <Route
          path="/"
          element={
            // <Suspense fallback={<Loading />}>
            <Countries
              countries={searchCountries}
              setSearchInput={search}
              page={page}
              setPage={setPage}
              region={region}
              setRegion={setRegion}
            />
            // </Suspense>
          }
        />
      </Routes>
    </AnimatePresence>
  );
}

export default Routess;
